import React, { memo, useCallback, useEffect, useState } from 'react'
import type { FC, ReactNode, KeyboardEvent, ChangeEvent } from 'react'
import { useParams } from 'react-router-dom'
import { appShallowEqual, useAppDispatch, useAppSelector } from '@/store'
import { generateMessageHistoryContainer } from '@/utils'
import { getWebSocket } from '@/service/socket'
import dayjs from 'dayjs'
import moreIcon from '@/svg/More Icon.svg'
import searchIcon from '@/svg/Search Messages.svg'
import callIcon from '@/svg/Call Icon.svg'
import emojiIcon from '@/svg/Emoji Icon.svg'
import sendIcon from '@/svg/Send Icon.svg'
import { addMessage } from '@/store/module/message'

interface IProps {
  children?: ReactNode
}

interface Message {
  mid: number
  fromUser: string
  toUser: string
  content: string
  sendTime: number
}
type MessagesList = Message[]

interface Friend {
  uid?: any
  username: string
  password?: any
  salt?: any
  gender?: any
  avatar?: string
  email?: any
  phone?: any
  createdTime?: any
  friendList?: any
  groupList?: any
}

const ChatPage: FC<IProps> = (props) => {
  const dispathch = useAppDispatch()
  const { chatId } = useParams() // 获取动态路由参数
  const [message, setMessage] = useState('') //保存消息内容
  const [messageContent, setMessageContent] = useState<MessagesList>([]) //保存所有历史消息
  const [friendInfo, setFriendInfo] = useState<Friend>({} as Friend)
  const dispatch = useAppDispatch()

  /**
   * 从messagestore获取好友列表、群组列表、好友信息记录、群组信息记录
   */
  const { friendsList, messagesList } = useAppSelector((state) => {
    return {
      friendsList: state.message.friendsList,
      messagesList: state.message.messagesList,
    }
  }, appShallowEqual)

  useEffect(() => {
    const messagesContainer = generateMessageHistoryContainer(
      friendsList,
      messagesList
    )
    let newFriendInfo: Friend = {} as Friend
    friendsList.forEach((item) => {
      if (item.username === chatId) {
        newFriendInfo = item
      }
    })
    setFriendInfo(newFriendInfo)
    messagesContainer.forEach((value, key) => {
      if (key.username === chatId) {
        setMessageContent(value)
      }
    })
  }, [chatId, friendsList, messagesList])

  /**
   * 输入框双向数据绑定
   */
  const changeMessageHandler = useCallback(
    (e: ChangeEvent<HTMLInputElement>) => {
      setMessage(e.target.value)
    },
    [message]
  )
  /**
   * 发送信息逻辑统一处理
   */
  const sendMessage = () => {
    const websocket = getWebSocket()
    const sendTime = new Date().getTime()
    const newMessgae = {
      toUser: chatId || '',
      fromUser: localStorage.getItem('username') || '',
      content: message,
      sendTime: sendTime,
    }
    dispathch(
      addMessage({
        toUser: chatId,
        fromUser: localStorage.getItem('username') || '',
        content: message,
        sendTime: sendTime,
        mid: '',
      })
    )
    websocket.emit('sendSingleMessageEvent', newMessgae)
    setMessage('')
  }
  /**
   * 处理键盘Enter发送信息
   */
  const enterDownHandler = (e: KeyboardEvent<HTMLDivElement>) => {
    if (e.code === 'Enter') {
      sendMessage()
    }
  }

  /**
   * 处理点击发送信息
   */
  const clickSendHandler = () => {
    sendMessage()
  }

  return (
    <div className="flex flex-col items-center h-full">
      {/* 顶部菜单栏 */}
      <div className="flex justify-between w-[57.25rem] h-[3.5rem] px-4 py-1 bg-white">
        {/* 左侧用户信息 */}
        <div className="flex items-center w-[46.25rem] ">
          <img
            className="w-10 h-10 rounded-full mr-4"
            src={friendInfo.avatar}
            alt=""
          />
          <div className="flex flex-col justify-between">
            <div>{friendInfo.username}</div>
            <div>{friendInfo.gender}</div>
          </div>
        </div>
        {/* 右侧菜单栏 */}
        <div className="flex items-center ">
          <img className="w-10 h-10 cursor-pointer" src={searchIcon} />
          <img className="w-10 h-10 cursor-pointer" src={callIcon} />
          <img className="w-10 h-10 cursor-pointer" src={moreIcon} />
        </div>
      </div>
      {/* 聊天区 */}
      <div className=" relative w-[43.375rem] h-[41.5rem] bg-transparent">
        {/* 聊天信息 */}
        <div className="flex flex-col justify-end h-[36.5rem] pb-4 overflow-auto">
          {messageContent.map((item, index) => {
            return (
              <div
                className={`flex  w-full mb-4 ${
                  item.fromUser === localStorage.getItem('username')
                    ? 'justify-end '
                    : 'justify-start'
                }`}
                key={index}
              >
                <div
                  className={`flex flex-col max-w-[25.125rem] px-3 py-1 rounded-xl ${
                    item.fromUser === localStorage.getItem('username')
                      ? 'bg-[#78E378]'
                      : 'bg-white'
                  }`}
                >
                  <div className=" max-w-[25.125rem] overflow-hidden text-ellipsis ">
                    {item.content}
                  </div>
                  <div className="flex justify-end mt-1 text-xs">
                    {dayjs(item.sendTime).format('YYYY.MM.DD hh.mm')}
                  </div>
                </div>
              </div>
            )
          })}
        </div>
        {/* 输入框 */}
        <div
          className="absolute bottom-6 w-full h-14 "
          onKeyDown={enterDownHandler}
        >
          <img
            className="absolute left-4 top-4 cursor-pointer"
            src={emojiIcon}
            alt=""
          />
          <input
            className="w-full h-full px-14 py-[1.125rem] rounded-xl bg-white outline-none"
            type="text"
            value={message}
            onChange={changeMessageHandler}
          />
          <img
            className="absolute right-4 top-4 cursor-pointer"
            src={sendIcon}
            onClick={clickSendHandler}
          />
        </div>
      </div>
    </div>
  )
}

export default memo(ChatPage)
