interface Friend {
  uid?: any
  username: string
  password?: any
  salt?: any
  gender?: any
  avatar?: string
  email?: any
  phone?: any
  createdTime?: any
  friendList?: any
  groupList?: any
}
interface Message {
  fromUser: string
  toUser: string
  content: string
  sendTime: number
  mid: number
}
type MessagesList = Message[]
type MessageHistoryContainer = Map<Friend, MessagesList>
export const generateMessageHistoryContainer = (
  friendsList: any,
  messagesList: any
): MessageHistoryContainer => {
  const map = new Map()

  friendsList.forEach((friend: any) => {
    map.set(friend, [])
    messagesList.forEach((message: any) => {
      if (
        friend.username === message.fromUser ||
        friend.username === message.toUser
      ) {
        map.get(friend).push(message)
      }
    })
  })
  return map
}
